import styled from 'styled-components';

const Strip = styled.div`
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 4rem;
  background: rgba(0, 0, 0, .7);
  display: flex;
  align-items: center;
  padding: 0 1rem;
`;

export default Strip;



